import { useState } from "react";
import { Stack, TextField, InputAdornment, IconButton } from "@mui/material";
import { useFormikContext } from "formik";
import FormTextFieldLabel from "./FormTextFieldLabel";
import VisibilityOutlinedIcon from '@mui/icons-material/VisibilityOutlined';
import VisibilityOffOutlinedIcon from '@mui/icons-material/VisibilityOffOutlined';

export default function FormTextField({ name, label, sx, inputSx, children, lowerCase = false, whiteSpace = false, emptyCheck = false, visibility, setVisibility, forgotPassword = false, onClickForgotPassword, ...otherProps }) {
  const [blur, setBlur] = useState(true);

  const { values, touched, errors, handleChange, handleBlur, dirty } = useFormikContext();

  const onChangeVisibility = () => {
    let val = visibility;
    setVisibility(!val);
  }

  return (
    <Stack sx={{ mb: "22px", ...sx }}>
      <FormTextFieldLabel label={label} forgotPassword={forgotPassword} onClickForgotPassword={onClickForgotPassword} />
      <TextField
        sx={{
          "& .MuiInputBase-input": {
            ...inputSx
          },
        }}
        fullWidth
        autoComplete="off"
        id={name}
        name={name}
        value={lowerCase ? values[name].toLowerCase().trim() || "" : whiteSpace ? values[name]?.replace(/^\s/g, '') || '' : values[name]?.trim() || ""}
        onChange={(e) => {
          handleChange(e);
          setBlur(false);
        }}
        onBlur={(e) => {
          handleBlur(e);
          var regex = /^\s*$/;
          if (!regex.test(values[name])) {
            setBlur(true);
          } else {
            setBlur(false);
          }
        }}
        // error={
        //   blur &&
        //   Boolean(values[name] || emptyCheck) &&
        //   touched[name] &&
        //   Boolean(errors[name])
        // }
        helperText={
          blur &&
          Boolean(values[name] || emptyCheck) &&
          (touched[name] || dirty[name]) &&
          errors[name]
        }
        {...otherProps}
        type={!visibility ? otherProps.type : 'text'}
        InputProps={{
          endAdornment: (
            otherProps.type === 'password' ?
              <InputAdornment position='end'>
                {
                  <IconButton onClick={() => onChangeVisibility()}>
                    {
                      visibility ?
                        <VisibilityOutlinedIcon style={{ color: '#8C8C8C', transform: 'scale(0.7274)' }} />
                        : <VisibilityOffOutlinedIcon style={{ color: '#8C8C8C', transform: 'scale(0.7274)' }} />}
                  </IconButton>
                }
              </InputAdornment>
              : ''
          ),
        }}
      >{children}</TextField>
    </Stack>
  );
}
